import React from "react"
import chroma from "chroma-js"
import isNaN from "lodash/isNaN"

import Tr from "elements/Tr"
import Div from "elements/Div"
import Flex from "components/Flex"
import P from "elements/P"
import TableCell from "components/TableCell"
import Code from "elements/Code"

const getHue = colorObj => {
  const hueVal = colorObj.get("hsl.h")

  if (isNaN(hueVal)) {
    return "Gray"
  }

  const names = [
    "Red", // 0
    "Orange", // 30
    "Yellow", // 60
    "Lime", // 90
    "Green", // 120
    "Teal", // 150
    "Cyan", // 180
    "Blue", // 210
    "Indigo", // 240
    "Violet", // 270
    "Fuschia", // 300
    "Pink", // 330
    "Red" // 360
  ]
  const hueIndex = Math.round((hueVal - 2) / 30)
  return names[hueIndex]
}

const useColorData = color => {
  const colorObj = chroma(color)
  const hue = getHue(colorObj)

  return {
    hex: colorObj.hex(),
    hsl: colorObj.css("hsl"),
    rgb: colorObj.css(),
    hue
  }
}

const Swatch = ({ color }) => (
  <Flex>
    <Div py={2} mr={2} borderRadius={1} bg={color} />
    <P my={0} fontWeight={2} color={color}>
      Aa
    </P>
  </Flex>
)

const ColorTableRow = ({ color, onClick }) => {
  const colorData = useColorData(color)
  return (
    <Tr>
      <TableCell style={{ cursor: "pointer" }}>
        <Swatch color={colorData.rgb} />
      </TableCell>
      <TableCell>{colorData.hue}</TableCell>
      <TableCell>
        <Code fontSize={1}>{colorData.hex}</Code>
      </TableCell>
      <TableCell>
        <Code fontSize={1}>{colorData.hsl}</Code>
      </TableCell>
      <TableCell>
        <Code fontSize={1}>{colorData.rgb}</Code>
      </TableCell>
    </Tr>
  )
}

export default ColorTableRow
